import "./assets/css/select.css!";
import jquery  = require("jquery");
import {TextInput} from "./text-input";
import {IRequestConfig} from "../http/interface/i-request-config";
import {ABaseInput} from "./abstract/a-base-input";
import {AInput} from "./abstract/a-input";
import {IDataComponent} from "../component/interface/i-data-component";
import {EventEmitter} from "../core/event-emitter";
import {IInputAddon} from "./interface/i-input-addon";
import {InputAddon} from "./input-addon";
import {EMouseEvent} from "../component/enum/e-mouse-event";
import {EInputEvent} from "../component/enum/e-input-event";

export interface ISelect<T> extends IDataComponent<T> {
	showList(visible: boolean): this;
	setValueField(field: string): this;
	getValueField(): string;
	setDescriptionField(field: string): this;
	getDescriptionField(): string;
	setPlaceholder(placeholder: string): this;
	getValue(): T;
	setValue(item: T): this;
	isValid(): boolean;
	getByValue(item: T): T;
	onSelect: EventEmitter<T[]>;
	getData(): T[];
}

export interface ISelectData {
	id: number | string;
	text: string;
	disabled?: boolean;
}

export interface ISelectOptions {
	listPosition?:"top"|"bottom";
	placeholder?: string;
	minSearchLength?:number;
	multiple?:boolean;
	searchIgnoreCase?:boolean;
	clearOnEmptyTerm?:boolean;
	searchDelayTime?:number;
	autocomplete?:boolean;
	searchSimbol?:string;
}

/**
 * @classdesc Componente utilizado para a seleção de registros
 *
 * <img src="../../doc_assets/select_example.png" />
 *
 * @example
 *
 * import {Select} from "mvcomponents/input";
 * 
 * interface IObjeto {
 *   id: number;
 *   descricao: string;
 * }
 * 
 * let dadosSelect:IObjeto[] = [
 *     {id: 1,descricao: "Primeiro item"},
 *     {id: 2,descricao: "Segundo item"}
 * ];
 * 
 * let select:Select<IObjeto> = new Select<IObjeto>()
 *      .setName("select")
 *      .setValueField("id")
 *      .setDescriptionField("descricao")
 *      .setData(dadosSelect);
 * 
 * this.append(select);
 *
 * @export
 * @extends {ABaseInput}
 * @implements {ISelect}
 * @memberof module:mvcomponents/input
 */
export class Select<T> extends ABaseInput<T> implements ISelect<T> {
	private config: ISelectOptions = {};
	private selector: JQuery;
	private descriptionField: string = "text";
	private valueField: string = "id";
	private labelAll:any;
	private assignedProperty: string;
	private data: T[];
	public onBeforeDropdown: EventEmitter<any> = new EventEmitter();
	public onSelect: EventEmitter<T[]> = new EventEmitter<T[]>();
	public onNoResultsFound:EventEmitter<string> = new EventEmitter<string>();
	public onClearMultiItem:EventEmitter<T> = new EventEmitter<T>();
	private loaded: boolean;
	private preValue:T;
	private btnShowList:InputAddon;
	private searchTerm:string;
	private iconUp:string;
	private iconDown:string;
	private fnDisplayListItem:(item:T)=>string;
	private fnDisplayField:(item:T)=>string;
	private fnNoResultsDisplay:(term: string) => string; 
	private selecteds:T[];   
	private searchDelayTimeIns:number;

	constructor(config?:ISelectOptions) {
		super("div", "");
		this.data = [];
		this.config = Object.assign({},{minSearchLength:0,listPosition:"bottom"},config||{});
		
		this.render();
		let $_this = this;

		if($_this.config){
			if($_this.config.searchIgnoreCase && $_this.config.autocomplete){
				console.warn(`"searchIgnoreCase" não deve ser usado com "autocomplete:true", pois o "searchIgnoreCase" não terá o comportamento adequado em requisições ajax!`);
			}
			if($_this.config.autocomplete && (!$_this.config.minSearchLength)){
				console.warn(`"minSearchLength" precisa está definido e ser maior que 0 para "autocomplete:true"`);
			}
			if($_this.config.clearOnEmptyTerm && (!$_this.config.minSearchLength)){
				console.warn(`"minSearchLength" precisa ser maior que 0 para "clearOnEmptyTerm:true"`);
			}
		}

		if(config && config.multiple){
			this.addStyleName("is-multiple");
			this.selecteds = [];
			this.fnDisplayField = item => `<span class="mult-select-item" data-id="${$_this.getValueByPath(item,$_this.getValueField())}">
											${$_this.getValueByPath(item,$_this.getDescriptionField())}
											<i class="mv-basico-fechar multi-icon mult-item-delete"></i>
										</span>`;
		}else{
			this.fnDisplayField = item => this.getValueByPath(item,this.getDescriptionField());
		}

		this.fnDisplayListItem = item => this.getValueByPath(item,this.getDescriptionField());
		this.fnNoResultsDisplay = term => `<span style="font-style:italic"><strong>${term}</strong> não encontrado!<span>`;

		this.iconUp = "glyphicon glyphicon-menu-up";
		this.iconDown = "glyphicon glyphicon-menu-down";

		this.btnShowList = new InputAddon()
				.setIcon(this.iconDown);

		this.append(this.btnShowList);

		this.btnShowList.addEvent(EMouseEvent.CLICK,()=>{ 
			//this.showList(!this.btnShowList.hasStyleName("is-menu-visible"));
			this.getInput().focus();
		});        

		this.getMenuListItem()
			.on("mouseover",function (){
				jquery(this).addClass("is-over");
			})
			.on("mouseout",function (){
				jquery(this).removeClass("is-over");
			});

		this.getDisplayItem()
			.on("click",function (){
				$_this.showFieldInput(true);
			})
			.on("click",".mult-select-item > .multi-icon",function (evt:Event){
				evt.preventDefault();
				evt.stopPropagation();
				let itemSlected = jquery(this);
				if(itemSlected.hasClass("mult-item-delete")){                    
					let tmpid = itemSlected.parent().attr("data-id");
					if(tmpid){
						$_this.selecteds.some((iten,indx) => {
							if(String($_this.getValueByPath(iten,$_this.getValueField())) === tmpid){
								$_this.selecteds.splice(indx,1);
								return true;
							}
							return false;
						});                        
						$_this.onClearMultiItem.emit($_this.getByValue(tmpid));
					}
					itemSlected.parent().remove();    
				}
			});

		this.getInput().on("focus",()=>{
			if(!this.getMenuListItem().hasClass("from-enter")){
				this.showList(true);
				$_this.showFieldInput(true,false);
			}else if(this.getMenuListItem().hasClass("from-enter")){
				$_this.showFieldInput(false,false);
				this.getMenuListItem().removeClass("from-enter");
				//this.showFieldInput(false);
			}            
		}).on("blur",(evt:Event)=>{
			//evt.preventDefault();
			if(!this.getMenuListItem().hasClass("is-over")){
				this.showList(false);   
			}else if(this.getMenuListItem().hasClass("from-enter")){
				//this.showFieldInput(true);
			}
			this.fakeFocus(false);
		}).on("keyup",function (evt:any){
			if(!$_this.isReadonly()){
				let txtSearch:string = jquery(this).val();
				let tmpContainer = $_this.getMenuListItem();

				if(evt.which === 38 || evt.which === 40){
					evt.preventDefault();
					evt.stopPropagation();
					let lastSelected = tmpContainer.find("li.display-item-list.is-active:first");
					tmpContainer.find("li.display-item-list.is-active").removeClass("is-active");
					if(evt.which === 38){
						//up
						if(lastSelected.hasClass("is-first")){
							lastSelected.addClass("is-active");
						}else if(lastSelected.length){                       
							lastSelected.prev().addClass("is-active");
						}else{
							tmpContainer.find("li.display-item-list:last").addClass("is-active");
						} 
					}else if(evt.which === 40){
						//down
						if(lastSelected.hasClass("is-last")){
							lastSelected.addClass("is-active");
						}else if(lastSelected.length){                       
							lastSelected.next().addClass("is-active");
						}else{
							tmpContainer.find("li.display-item-list:first").addClass("is-active");
						}                   
					}
					return false;
				}else if(evt.which === 13){
					//enter
					let tmpItem = tmpContainer.find("li.display-item-list.is-active:first");
					if(tmpItem.length){                    
						let itemT:T = $_this.getByValue(tmpItem.attr("data-value"));
						$_this.setValue(itemT);  
						//$_this.getDisplayItem().html($_this.fnDisplayField(itemT));
						$_this.fakeFocus(true);                    
						$_this.showList(false);
						tmpContainer.addClass("from-enter");
						$_this.getInput().focus();
						$_this.onSelect.emit([$_this.getValue()]);
					}
					return;
				}else if(!(evt.which === 9 || evt.which === 16)){
					//pesquisa de texto normal

					let delaySearch:number = 0;
					if($_this.config && $_this.config.searchDelayTime){
						delaySearch = $_this.config.searchDelayTime;
					}

					window.clearTimeout($_this.searchDelayTimeIns);

					$_this.searchDelayTimeIns = window.setTimeout(()=>{
						$_this.searchTerm = txtSearch; 
						if(txtSearch.length === 0){
							$_this.preValue = null;
							$_this.setValue(null);
						}
						if($_this.config && $_this.config.clearOnEmptyTerm && txtSearch.length === 0){
							$_this.setData([]);
						}

						$_this.showFieldInput(true,false);
						$_this.showList(true);

						let tmpList:T[] = [];
						
						if($_this.config && $_this.config.searchSimbol && txtSearch.length && txtSearch.indexOf($_this.config.searchSimbol) > -1){
								tmpContainer
									.find("li.no-result-display")
									.addClass("is-visible")
									.removeClass("is-hidden")
									.html($_this.fnNoResultsDisplay(txtSearch));
								$_this.onNoResultsFound.emit(txtSearch);
						}else if($_this.config && $_this.config.autocomplete && txtSearch.length >= $_this.config.minSearchLength){
							if(!$_this.config.minSearchLength){
								console.warn("minSearchLength precisa está definido e ser maior que 0");
							}else{
								// ignora se existe ou nao dados para fazer a requisicao.
								tmpContainer
									.find("li.no-result-display")
									.addClass("is-visible")
									.removeClass("is-hidden")
									.html($_this.fnNoResultsDisplay(txtSearch));
								$_this.onNoResultsFound.emit(txtSearch); 
							}
						}else if(!tmpList.length && txtSearch.length >= $_this.config.minSearchLength){
							if($_this.config && $_this.config.searchIgnoreCase){
								let txtSearchIC = txtSearch.toLowerCase();
								tmpList = $_this.data.filter(item=>{
									let valueGroup = $_this.getValueByPath(item,$_this.getDescriptionField());
									return valueGroup.toLowerCase().indexOf(txtSearchIC) > -1; 
								});
							}else{
								tmpList = $_this.data.filter(item=>{
									let valueGroup = $_this.getValueByPath(item,$_this.getDescriptionField());
									return valueGroup.indexOf(txtSearch) > -1; 
								});
							}

							$_this.setList(tmpList);   

							if (tmpList.length) {
								tmpContainer
									.find("li.no-result-display")
									.removeClass("is-visible")
									.addClass("is-hidden");
							} else {
								tmpContainer
									.find("li.no-result-display")
									.addClass("is-visible")
									.removeClass("is-hidden")
									.html($_this.fnNoResultsDisplay(txtSearch));
								$_this.onNoResultsFound.emit(txtSearch);
							}                            
						}

					},delaySearch);

				}
			}
		});

	}
	private getValueByPath(item:any,path:string){
		let valueGroup = item;
		if(path.indexOf(".") > -1){
			valueGroup = path.split(".")
							.reduce( function ( prev, curr ) {
								return prev?prev[curr]:null;
							}, item || this );
		}else{
			valueGroup = item[path];
		}
		return valueGroup;
	}
	
	private getMenuListItem():JQuery{
		return jquery(this.element).find("ul:first");
	}
	private getDisplayItem():JQuery{
		return jquery(this.element).find(".input-group:first > .display-item-input:first");
	}
	private showFieldInput(showInput:boolean,forceFocus:boolean=true):this{
		this.getInput().removeClass(showInput?"is-hidden":"is-visible").addClass(showInput?"is-visible":"is-hidden");
		this.getDisplayItem().removeClass(showInput?"is-visible":"is-hidden").addClass(showInput?"is-hidden":"is-visible");
		if(showInput && forceFocus){
			this.getInput().focus();
		}
		return this;
	}
	private render() {
		let $ = jquery(this.element);
		$.addClass(`Select form-group col-xs-12 col-sm-4 col-md-4 is-list-${this.config.listPosition}`);
		$.append(`
			<label class="control-label" for="input_${this._uid}" style="display:none"></label>
			<div class="col-xs-12 input-group input-group-sm">
				<div class="form-control display-item-input is-visible"></div>
				<input class="form-control is-hidden" id="input_${this._uid}" autocomplete="off">
				<ul class="options-container is-hidden" id="input_${this._uid}_"></ul>
			</div>
		`);

		this.getInput().css("width", "100%");
		let $this_ = this;
		this.getMenuListItem().on("click","li.display-item-list",function (){
			let vlSelected =  jquery(this).attr("data-value");
			if($this_.labelAll && vlSelected && vlSelected === "-1"){
				$this_.setValue($this_.labelAll);
				$this_.onSelect.emit([null]); 
			}else{
				let indexItem:number = -1;
				let hasItem:boolean = $this_.data.some((item,indx)=>{
					if(String($this_.getValueByPath(item,$this_.getValueField())) === vlSelected){                    
						//$this_.preValue = item;
						indexItem = indx;
						//$this_.setValue(item);
						//$this_.getDisplayItem().html($this_.fnDisplayField(item));
						return true;
					}
					return false;
				});

				if(hasItem){
					if($this_.config && $this_.config.multiple){
						let hasItemInSelectes:boolean = $this_.selecteds.some((item,indx)=>{
							if(String($this_.getValueByPath(item,$this_.getValueField())) === vlSelected){
								return true;
							}
							return false;
						}); 
						if(!hasItemInSelectes){
							$this_.selecteds.unshift($this_.data[indexItem]);     
						}
					}       

					$this_.setValue($this_.data[indexItem]);

					$this_.onSelect.emit([$this_.getValue()]);                
				}
			}
			$this_.showFieldInput(false);
			$this_.showList(false);
			$this_.fakeFocus(true);
			$this_.getMenuListItem().addClass("from-enter");
			$this_.getInput().focus();
		});
	}
	
	private fakeFocus(on:boolean):this{        
		if(on){
			this.getDisplayItem().addClass("is-focus");
		}else{
			this.getDisplayItem().removeClass("is-focus");
		}        
		return this;
	}

	private normalizeWidth():void{
		if(!this.hasStyleName("was-initialized")){            
			let subInput = jquery(this.element).find(".select2-selection__rendered:first");
			let widthRendered:number = subInput.width();//jquery(this.element).width();
			if(widthRendered > 0){
				subInput.css({width:`${(widthRendered+28)}px`});
				this.addStyleName("was-initialized");
			}            
		}
	}
	protected getInput(): JQuery {
		return jquery(this.element).find("input:first");
	}
	private updateValueByPath(value:any, pathObj:string, tree:any, branch = tree):any {
		let path = pathObj.split(".");
		let last = path.length === 1;
		branch[path[0]] = last ? value : branch[path[0]]?branch[path[0]]:{};
		return last ? tree : this.updateValueByPath(value, path.slice(1).join("."), tree, branch[path[0]]);
	}
	public setLabelAll(labelAll:string):this{
		this.labelAll = {};
		this.updateValueByPath("-1",this.assignedProperty?this.assignedProperty:this.getValueField(),this.labelAll);
		this.updateValueByPath(labelAll,this.getDescriptionField(),this.labelAll);
		this.setValue(this.labelAll);
		return this;
	}
	private setList(data:T[]):this{
		let $_this = this;
		let select = this.getMenuListItem();
		let innerHTML = "";
		if(this.labelAll){
			innerHTML += `<li 
							data-index="0" 
							class="is-first display-item-list" 
							data-value="${this.getValueByPath(this.labelAll,this.getValueField())}"
							>
							${this.getValueByPath(this.labelAll,this.getDescriptionField())}
							</li>`;
		}
		let tmList:number = data.length;       
		data.forEach((dta,indx)=>{
			if(this.labelAll){
				indx += 1;
			}
			//let positionInd:string = "";
			let positionInd:string = (indx === 0?"is-first":"");
			if(!positionInd){
				positionInd = (indx+1 === tmList?"is-last":"");
			}
			innerHTML += `<li 
				data-index="${indx}" 
				class="${positionInd} display-item-list" 
				data-value="${$_this.getValueByPath(dta,$_this.getValueField())}"
				>
				${this.fnDisplayListItem(dta)}    
			</li>`;
		});

		innerHTML+=`<li class="no-result-display is-hidden"></li>`;

		select.html(innerHTML);
		return this;
	}
	private init() {
		this.setList(this.data);
		return this.getInput();
	}

	public refresh(refreshData: boolean = false): this {
		this.init();
		return this;
	}

	protected insertAddon(method: string, addon: IInputAddon): this {
		let $container = jquery(this.element).children("div.input-group");
		$container[method](addon.element);
		return this;
	}

	public append(addon: IInputAddon): this {
		return this.insertAddon("append", addon);
	}

	public prepend(addon: IInputAddon): this {
		return this.insertAddon("prepend", addon);
	}

	/**
	 * Altera a exibição na lista quando um termo não for encontrado
	 * 
	 * @example
	 * 
	 * let select:Select<IObjeto> = new Select<IObjeto>()
	 *     .setNoResults(term => `o item não foi encontrado!`);
	 * 
	 * @param {Function}
	 * @returns {Select}
	 */
	public setNoResults(resultDisplay: (term: string) => string): this {
		this.fnNoResultsDisplay = resultDisplay;
		return this;
	}

	/**
	 * Altera o "placeholder" do Select
	 * 
	 * @example
	 * 
	 * let select:Select<IObjeto> = new Select<IObjeto>()
	 *     .setPlaceholder("Selecione um item..");
	 * 
	 * @param {string} placeholder Texto que irá aparecer para auxiliar o usuário.
	 * @returns {Select}
	 */
	public setPlaceholder(placeholder: string): this {
		this.config.placeholder = placeholder;
		this.getInput().attr("placeholder",placeholder);
		this.getDisplayItem().html(`<span class="is-placeholder">${placeholder}</span>`);
		return this;
	}
	public showList(visible: boolean): this {        
		let input = this.getInput();
		if(input.prop("disabled")){
			return this;
		}
		this.onBeforeDropdown.emit(null).done(() => {
			let select = this.getMenuListItem();
			if(visible){
				select.removeClass("is-hidden");
				this.btnShowList.addStyleName("is-menu-visible").setIcon(this.iconUp);                
			}else{
				select.addClass("is-hidden");
				this.btnShowList.removeStyleName("is-menu-visible").setIcon(this.iconDown);
			}              
		});
		return this;
	}

	/**
	 * Informa qual campo identifica o valor
	 *
	 * @param {string} field identifica o valor
	 * @returns {this}
	 */
	public setValueField(field: string): this {
		this.valueField = field;
		return this.data ? this.refresh(true) : this;
	}

	/**
	 * Informa qual atributo o formulário irá manipular durante o getData
	 *
	 * @param {string} field identifica o valor
	 * @returns {this}
	 */
	public setAssignedProperty(field: string): this {
		this.assignedProperty = field;
		return this.data ? this.refresh(true) : this;
	}

	/**
	 * Informa qual atributo o formulário irá manipular durante o getData
	 *
	 * @param {string} field identifica o valor
	 * @returns {this}
	 */
	public getAssignedProperty(): string {
		return this.assignedProperty;
	}

	/**
	 * Informa qual é o campo descritivo dos dados
	 *
	 * @param {string} field descritivo dos dados
	 * @returns {this}
	 */
	public setDescriptionField(field: string): this {
		this.descriptionField = field;
		return this.data ? this.refresh(true) : this;
	}

	/**
	 * Retorna qual é o campo que identifica o valor
	 *
	 * @returns {string}
	 */
	public getValueField(): string {
		return this.valueField;
	}

	/**
	 * Retorna qual é o campo descritivo
	 *
	 * @returns {string}
	 */
	public getDescriptionField(): string {
		return this.descriptionField;
	}

	/**
	 * Atualiza os dados disponíveis para a seleção
	 *
	 * @param {any[]} data Dados disponíveis no Select.
	 * @returns {this}
	 */
	public setData(data: T[]): this {
		this.data = data;
		if(this.config && this.config.multiple){
			this.selecteds = [];
		}
		this.refresh(true);
		if(this.preValue){
			this.setValue(this.preValue);
		}
		return this;
	}

	/**
	 * @returns {T[]}
	 */
	public getData(): T[] {
		return this.data;
	}

	/**
	 * Remove todos os itens
	 *
	 * @returns {this}
	 */
	public clear(): this {
		//this.setData([]);
		this.setValues([]);
		if(this.labelAll){
			this.setValue(this.labelAll);
		}
		return this;
	}

	/**
	 * Recupera o valor selecionado no Select.
	 *
	 * @return {any} Retorna de acordo com Objeto definido na instancia do Select.
	 */
	public getValue(): T {
		let item:any = null;
		if(this.getAttribute("data-value") === "-1"){
			item = null;
			if(this.assignedProperty){
				item = this.getValueByPath(this.labelAll,this.assignedProperty);
			}else{
				item = "";
			}
		}else{
			item = this.getByValue(this.getAttribute("data-value"));
		}
		return item;
	}

	/**
	 * Recupera os valores selecionados no Select caso a opção multiple esteja configurada como true.
	 *
	 * @return {any[]} Retorna uma lista de objetos de acordo com Objeto definido na instancia do Select.
	 */
	public getValues(): T[] {
		return this.selecteds||[];
	}

	/**
	 * Altera a lista de itens selecionados caso a opção multiple esteja configurada como true.
	 *
	 * @returns {Select}
	 */
	public setValues(itens: T[]):this{
		this.selecteds = itens;
		this.setValue(itens[0]||null);
		return this;
	}

	/**
	 * Adiciona itens a lista de itens selecionados caso a opção multiple esteja configurada como true.
	 *
	 * @returns {Select}
	 */
	public addValues(itens: T[]):this{
		this.selecteds = this.selecteds.concat(itens);
		this.setValue(this.selecteds[0]||null);
		return this;
	}

	/**
	 * Atualiza o valor do input com base nos dados carregados
	 * @example
	 * 
	 * itSelect.setValue({id:1});
	 *
	 * @param {any} value Objeto de acordo definido na instancia do Select.
	 * @returns {Select}
	 */
	public setValue(item: T): this {
		if (item) {
			let tmpItem:T = null;
			if(this.labelAll && this.getValueByPath(this.labelAll,this.getValueField()) === this.getValueByPath(item,this.getValueField())){
				tmpItem = this.labelAll;
			}else{
				tmpItem = this.getByValue(this.getValueByPath(item,this.getValueField()));
			}
			if(tmpItem){
				this.setAttribute("data-value",this.getValueByPath(item,this.getValueField()));
				let valueGroup = this.getValueByPath(tmpItem,this.getDescriptionField());
				this.getInput().val(valueGroup); 

				if(this.config && this.config.multiple){
					let concatedListStr:string = "";
					this.selecteds.forEach(item=>concatedListStr += this.fnDisplayField(item));
					this.getDisplayItem().html(concatedListStr);
				}else{
					this.getDisplayItem().html(this.fnDisplayField(tmpItem));
				} 

				this.preValue = null; 
			}else{
				this.preValue = item;
			}           
		} else {
			this.setAttribute("data-value","");
			this.getInput().val("");
			this.getDisplayItem().html("");   
			this.selecteds = [];         
			if(this.labelAll){
				this.setValue(this.labelAll);
			}
		}
		if(this.isDirty()){
			jquery(this.element).trigger("change");
		}        
		return this;
	}

	public getRawValue(): string {
		//return this.getByValue(this.getValue());
		return null;
	}
	
	/**
	 * Recupera o Objeto definido na instancia do Select de acordo com valor.
	 *
	 * @return {any} Retorna de acordo com Objeto definido na instancia do Select.
	 */
	public getByValue(value: any): T {
		if(this.data && this.data.length){
			let field: string = this.getValueField();
			let itemFound = this
					.data
					.find(item => String(value) === String(this.getValueByPath(item,field)));
			return itemFound || null;
		}
		return null;		
	}

	/**
	 * Altera a exibição dos itens do select quando a lista estiver aberta
	 * 
	 * @example
	 * 
	 * let select:Select<IObjeto> = new Select<IObjeto>()
	 *     .setDisplayListItem(item => `<strong>Nome:${item.nome}</strong>`);
	 * 
	 * @param {Function}
	 * @returns {Select}
	 */
	public setDisplayListItem(display:(item:T)=>string):this{
		this.fnDisplayListItem = display;
		return this;
	}

	/**
	 * Altera a exibição do item do select quando um item form selecionado
	 * 
	 * @example
	 * 
	 * let select:Select<IObjeto> = new Select<IObjeto>()
	 *     .setDisplayItem(item => `<strong>Nome:${item.nome}</strong>`);
	 * 
	 * @param {Function}
	 * @returns {Select}
	 */    
	public setDisplayItem(display:(item:T)=>string):this{
		this.fnDisplayField = display;
		return this;
	}
} 
